<template>
  <section class="myprize">
    <div class="prize-item" v-for="(item, index) in myPrizeList" :key="index">
      <div class="top flex-row-center">
        <div class="left flex-row-center">
          <div><em class="tag">¥</em><em class="price">{{item.price}}</em></div>
        </div>
        <div class="right flex-row-center">
          <div class="text">
            <div class="title">{{ item.name }}</div>
            <div class="subtitle">有效期 2005-05-01</div>
            <div class="subtitle" @click="openDesc(index)">
              使用规则
              <img
                v-if="!item.isshowdesc"
                src="//x.autoimg.cn/2sc/2021/2021-2/circle-arrow-down.png"
              />
              <img
                v-if="item.isshowdesc"
                src="//x.autoimg.cn/2sc/2021/2021-2/circle-arrow-up.png"
              />
            </div>
          </div>
          <div class="right-btn">
            <div class="btn flex-row-center" @click="btnClick(item)">
              {{ item.isreal === 1 ? "修改地址" : "立即使用" }}
            </div>
          </div>
        </div>
      </div>
      <div class="bottom" v-show="item.isshowdesc">{{ item.desc }}</div>
    </div>
  </section>
</template>

<script>
//我的奖品
import vue from "vue";
import { Toast } from "vant";
vue.use(Toast);

import { isEscApp, isManApp, isWechat } from "../../util/util";
import { API } from "../../api/BlindBox";

export default {
  name: "MyPrize",
  data() {
    return {
      isInApp: false,
      myPrizeList: [
        // {
        //   name: "平安保险",
        //   desc: `1、本活动为汽车之家用户专享。
        // 2、在超品特价车活动车源下，用户支付定金，获得该车源所展示权益，用户可以在“汽车之家App-我的-我的订单”中找到订单。
        // 3、权益一：购车返现1500元`,
        //   isreal: 0, //0：非实物 1：实物
        //   url:
        //     "https://a.dmall.com/act/QZyWIoHfTexphDPO.html?nopos=0&tpc=a_168271",
        //   isshowdesc: false,
        // },
        // {
        //   name: "e代驾代驾券",
        //   desc: `1、本活动为汽车之家用户专享。
        // 2、在超品特价车活动车源下，用户支付定金，获得该车源所展示权益，用户可以在“汽车之家App-我的-我的订单”中找到订单。
        // 3、权益一：购车返现1500元`,
        //   isreal: 0, //0：非实物 1：实物
        //   url:
        //     "https://a.dmall.com/act/QZyWIoHfTexphDPO.html?nopos=0&tpc=a_168271",
        //   isshowdesc: false,
        // },
        // {
        //   name: "乐高积木布加迪",
        //   desc: `1、本活动为汽车之家用户专享。
        // 2、在超品特价车活动车源下，用户支付定金，获得该车源所展示权益，用户可以在“汽车之家App-我的-我的订单”中找到订单。
        // 3、权益一：购车返现1500元`,
        //   isreal: 1, //0：非实物 1：实物
        //   url:
        //     "https://a.dmall.com/act/QZyWIoHfTexphDPO.html?nopos=0&tpc=a_168271",
        //   isshowdesc: false,
        // },
      ], //我的奖品
    };
  },
  components: {},
  props: {},
  created() {
    this.isInApp = !(isEscApp() || isManApp() || isWechat());
    this.getMyPrize();
  },
  mounted() {},
  methods: {
    //我的奖品
    getMyPrize() {
      API.GET_MY_PRIZE_LIST({})
        .then(({ returncode, result }) => {
          console.log("getMyPrize - > ", returncode, result);
          if (+returncode === 0) {
            result.forEach((item, index) => {
              item.isshowdesc = false;
              item.index = index;
            });
            console.log(result);
            this.myPrizeList = result;
          }
        })
        .catch((err) => {
          console.log("getMyPrize - 报错 - >", err);
        });
    },

    //按钮点击
    btnClick(item) {
      if (+item.isreal === 1) {
        
      } else {
        location.href = item.url;
      }
    },
    //展示说明
    openDesc(index) {
      this.myPrizeList.forEach((item, i) => {
        if (index === i) {
          item.isshowdesc = !item.isshowdesc;
        } else {
          item.isshowdesc = false;
        }
      });
    },
  },
};
</script>

<style lang="scss">
.myprize {
  padding: 15px;
  box-sizing: border-box;
  width: 100%;
  .prize-item {
    width: 100%;
    min-height: 95px;
    background: #ffffff;
    box-shadow: 0px 0px 18px 0px rgba(0, 0, 0, 0.07);
    border-radius: 8px;
    padding-bottom: 1px;
    margin-bottom: 10px;
    .top {
      .left {
        width: 28.69%;
        height: 95px;
        .tag {
          font-family: DINAlternate-Bold;
          font-size: 18px;
          color: #fb7207;
          letter-spacing: 0.45px;
          font-weight: 700;
        }
        .price {
          font-family: DINAlternate-Bold;
          font-size: 36px;
          color: #fb7207;
          letter-spacing: 0;
          font-weight: 700;
        }
      }
      .left::after {
        content: "";
        position: absolute;
        left: 28.69%;
        display: inline-block;
        width: 1px;
        height: 70px;
        border-right: 1px dotted #cccccc;
      }
      .right {
        width: 71.31%;
        padding: 17px 15px auto 13px;
        .text {
          padding-left: 5.28%;
          width: 63.29%;
          .title {
            font-family: PingFangSC-Semibold;
            font-size: 15px;
            color: #1b1b1b;
            letter-spacing: 0.38px;
            font-weight: 600;
            line-height: 24px;
          }
          .subtitle {
            font-family: PingFangSC-Regular;
            font-size: 12px;
            color: #666666;
            line-height: 24px;
            font-weight: 400;
            img {
              width: 12px;
              height: 12px;
              margin-top:6px;
            }
          }
        }
        .right-btn {
          width: 44.71%;
          .btn {
            width: 80px;
            height: 28px;
            background-image: linear-gradient(0deg, #ff5338 0%, #ff985f 100%);
            border-radius: 20px;

            font-family: PingFangSC-Semibold;
            font-size: 12px;
            color: #ffffff;
            letter-spacing: 0;
            text-align: center;
            font-weight: 600;
          }
        }
      }
    }
    .bottom {
      border-top: 1px solid #cccccc;
      margin: 5px 20px 15px;
      padding-top: 8px;
      font-family: PingFangSC-Regular;
      font-size: 12px;
      color: #999999;
      letter-spacing: 0.3px;
      line-height: 18px;
      font-weight: 400;
    }
  }
}
</style>


